function bindleftCommonSearchdropdown(ths, modulename, filtername) {
    var vm = ths;
    let url=filtername;
    let params = {};
    params["moduleName"] = modulename; 
    params["type"] = filtername
    CompanyHolidayDataService.GetMainReportSearching(url)
        .then(function (response) {
            var json = response.data;
            console.log('folderid', json);
            if (json != null) {
                if (filtername == "FOLDER_NAME") {
                        vm.leftSearchFields[4].listOptions =json.map((item) => {
                        const container = {};                    
                        container.name = item.FOLDER_NAME;
                        container.value = item.FOLDER_ID.toString();                    
                        return container;
                    })                   
                        vm.LeadStatusList = json.map((items) => {
                        const container = {};                    
                        container.name = items.FOLDER_NAME;
                        container.value = items.FOLDER_ID.toString();                    
                        return container;
                    })                    
                }  
                if (filtername == "CREATED_BY") {
                        vm.leftSearchFields[3].listOptions =json.map((value) => {
                        const container = {};                    
                        container.name = value.CREATED_BY;
                        container.value = value.CREATED_ID.toString();                    
                        return container;
                    })                   
                        vm.LeadStatusList = json.map((values) => {
                        const container = {};                    
                        container.name = values.CREATED_BY;
                        container.value = values.CREATED_ID.toString();                    
                        return container;
                    }) 
                } 
                if (filtername == "MODIFIED_BY") {
                        vm.leftSearchFields[7].listOptions =json.map((data) => {
                        const container = {};                    
                        container.name = data.MODIFIED_BY;
                        container.value = data.MODIFIED_ID.toString();                    
                        return container;
                    })                   
                        vm.LeadStatusList = json.map((dataval) => {
                        const container = {};                    
                        container.name = dataval.MODIFIED_BY;
                        container.value = dataval.MODIFIED_ID.toString();                    
                        return container;
                    }) 
                } 
            }
    });
}
function LocationbindleftCommonSearchdropdown(){
    var vm = this;
    var url=`RequestFrom=${''}`;
    CompanyHolidayDataService.CompanyHolidayMultipleLocation(url)
        .then(function (response) {
            if (response.data.result != "" && response.data.result != null) {
                if (response.data.result.length > 0) {
                    // var LocationData=response.data.result;
                    response.data.result.forEach(element => {                        
                        let Obj={
                            value:element.COMPANY_LOCATION_ID,
                            name:element.LOCATION_NAME
                        }
                        vm.topsearchSchema[1].listOptions.push(Obj);
                    });
                }
                
            }
        });
    }

        function GetCompanyParentClientListByCompanyId(){
            var vm = this;
            var url=`RequestFrom=${'CUSTOM'}`;
            CompanyHolidayDataService.GetClientByCompanyId(url)
                .then(function (response) {
                    
                    if (response.data.result != "" && response.data.result != null) {
                        if (response.data.result.length > 0) {
                            // var LocationData=response.data.result;
                            response.data.result.forEach(element => {                        
                                let Obj={
                                    value:element.CHILD_USER_ID,
                                    name:element.CHILD_NAME
                                }
                                vm.topsearchSchema[6].listOptions.push(Obj);
                            });
                        }
                        
                    }
                });
}
